import "./app.scss"
import React from "react"
import { Routes, Route } from 'react-router-dom'
import Navbar from "./components/navbar/navbar"
import Home from "./components/home/home"
import Projects from "./components/projects/projects"
import About from "./components/about/about"
import Skills from "./components/skills/skills"
import Contact from "./components/contact/contact"
import Newsletter from "./components/newsletter/newsletter"
import Pagenotfound from "./components/pagenotfound/pagenotfound"
import Blogs from "./components/blogs/blogs"
import dark from "./dark.png"
import light from "./light.png"
import wmenu from "./wmenu.png"
import bmenu from "./bmenu.png"
import Footer from "./components/footer/footer"

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      theme: "",
      src : null,
      menu:null,
    };
  }


  componentDidMount() {
    const storedTheme = localStorage.getItem('theme');
    const storedMenu = localStorage.getItem('menu');
    const storedSrc = localStorage.getItem('src');
    if (storedTheme && storedMenu && storedSrc) {
      this.setState({ theme: storedTheme });
      this.setState({ src: storedSrc });
      this.setState({ menu: storedMenu });

    }
    else {
      this.setState({ theme: "dark" })
      this.setState({ src: light });
      this.setState({ menu: wmenu });
    }


  }


  changeIt = () => {
    const newTheme = this.state.theme === 'dark' ? 'light' : 'dark';
    const newSrc = this.state.theme === 'dark' ? dark : light;
    const newMenu = this.state.theme === 'dark' ? bmenu : wmenu;
    localStorage.setItem('theme', newTheme);
    localStorage.setItem('src', newSrc);
    localStorage.setItem('menu', newMenu);
    this.setState({ theme: newTheme });
    this.setState({ src: newSrc });
    this.setState({ menu: newMenu });
  };




  render() {


    return (
      <div className={this.state.theme == "light" ? "App App-light" : "App"} >

        {/* importing navigation bar component */}
        <Navbar changeIt={this.changeIt} light={this.state.theme} source={this.state.src} menu={this.state.menu} />
        {/* <img className="mode" onClick={this.changeIt}  src={dark} /> */}


        {/* React router */}
        <Routes>
          <Route exact path="/" element={<Home light={this.state.theme} />} />
          <Route exact path="/projects" element={<Projects light={this.state.theme} />} />
          <Route exact path="/about" element={<About light={this.state.theme} />} />
          <Route exact path="/skills" element={<Skills light={this.state.theme} />} />
          <Route exact path="/contact" element={<Contact light={this.state.theme} />} />
          {/* <Route exact path="/newsletter" element={<Newsletter light={this.state.theme} />} /> */}
          <Route exact path="/blogs" element={<Blogs light={this.state.theme} />} />
          <Route path="*" element={<Pagenotfound />} />
        </Routes>

        <Footer light={this.state.theme} />
      </div>
    )
  }
}

export default App