import "./newsletter.scss"
import { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import useWeb3Forms from "@web3forms/react";
import ScrollToTop from "react-scroll-to-top";



function Newsletter({ light }) {

    const {
        register,
        handleSubmit,
        reset,
        watch,
        control,
        setValue,
        formState: { errors, isSubmitSuccessful, isSubmitting },
    } = useForm({
        mode: "onTouched",
    });
    const [isSuccess, setIsSuccess] = useState(false);
    const [message, setMessage] = useState(false);

    // Please update the Access Key in the .env
    const apiKey = process.env.PUBLIC_ACCESS_KEY || "2ce64ebd-7097-4f9a-bafb-3d7f49b4b372";

    const { submit: onSubmit } = useWeb3Forms({
        access_key: apiKey,
        settings: {
            from_name: "SKk",
            subject: "Newsletter-skk",
        },
        onSuccess: (msg, data) => {
            setIsSuccess(true);
            setMessage(msg);
            reset();
        },
        onError: (msg, data) => {
            setIsSuccess(false);
            setMessage(msg);
        },
    });

    const clearMsg = () => {
        setTimeout(() => {
            document.getElementById("sent-message-one").style.display = "none";
        }, 5000)
    }
    const clearMsgTwo = () => {
        setTimeout(() => {
            document.getElementById("sent-message-two").style.display = "none";
        }, 5000)
    }


    return (
        <div className="Newsletter">


            <div className={light == "light" ? "contact-container contact-container-light" : "contact-container"}>
                {/* <h2 className={light ? "news-text news-text-light" : "news-text"} >Get latest updates directly in your inbox😊</h2> */}
                <p className={light == "light" ? "news-sub news-sub-light" : "news-sub"}>Enter your email address to subscribe</p><br /> 
                <form onSubmit={handleSubmit(onSubmit)}  className={light == "light" ? "contact-form contact-form-light" : "contact-form"}>





                    <input
                        id="email_address"
                        type="email"
                        placeholder="Email Address"
                        name="email"
                        autoComplete="false"
                        required
                        className="contact-inputs"
                        {...register("email")}
                    />






                    <button
                        type="submit"
                        className={light == "light" ? "submit-contact submit-contact-light" : "submit-contact"}
                    >
                        Submit

                    </button>
                </form>
                {isSubmitSuccessful && isSuccess && (
                    <>
                        <p className="sub-search" id="sent-message-one">
                            Subscribed successfully!
                        </p>
                        {
                            clearMsg()

                        }

                    </>

                )}
                {isSubmitSuccessful && !isSuccess && (
                    <>
                        <p className="sub-search" id="sent-message-two">
                            Something went wrong. Please try again.
                        </p>
                        {
                            clearMsgTwo()

                        }
                    </>
                )}







            </div>

            <ScrollToTop smooth />
        </div>

    )
}

export default Newsletter;