import "./contact.scss"
import { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import useWeb3Forms from "@web3forms/react";
import discord from "./discord.png"
import instagram from "./instagram.png"
import github from "./github.png"
import linkedin from "./linkedin.png"
import twitter from "./twitter.png"
import ScrollToTop from "react-scroll-to-top";
import Bounce from "react-reveal/Bounce"
import Slide from 'react-reveal/Slide';



function Contact({ light }) {

    const {
        register,
        handleSubmit,
        reset,
        watch,
        control,
        setValue,
        formState: { errors, isSubmitSuccessful, isSubmitting },
    } = useForm({
        mode: "onTouched",
    });
    const [isSuccess, setIsSuccess] = useState(false);
    const [message, setMessage] = useState(false);

    // Please update the Access Key in the .env
    const apiKey = process.env.PUBLIC_ACCESS_KEY || "2ce64ebd-7097-4f9a-bafb-3d7f49b4b372";

    const { submit: onSubmit } = useWeb3Forms({
        access_key: apiKey,
        settings: {
            from_name: "SKk",
            subject: "New Message Sahil",
        },
        onSuccess: (msg, data) => {
            setIsSuccess(true);
            setMessage(msg);
            reset();
        },
        onError: (msg, data) => {
            setIsSuccess(false);
            setMessage(msg);
        },
    });

    const clearMsg = () => {
        setTimeout(() => {
            document.getElementById("sent-message-one").style.display = "none";
        }, 5000)
    }
    const clearMsgTwo = () => {
        setTimeout(() => {
            document.getElementById("sent-message-two").style.display = "none";
        }, 5000)
    }


    return (
        <div className="Contact">
            <Bounce left> <p className={light == "light" ? "headingg headingg-light" : "headingg"} >Contact</p></Bounce><br/>
            <div className="contact-box-container">
                <center>
                    <Slide bottom>
                        <div className={light == "light" ? "contact-container contact-container-light" : "contact-container"}>
                            {/* <p className={light == "light" ? "sub-search sub-search-light" : "sub-search"} id="cf" >I am open to suggestions and feedbacks.</p><br /> */}
                            <form onSubmit={handleSubmit(onSubmit)} className={light == "light" ? "contact-form contact-form-light" : "contact-form"}>


                                <input
                                    type="text"
                                    placeholder="Full Name"
                                    autoComplete="false"
                                    required
                                    className={light == "light" ? "contact-inputs contact-inputs-light" : "contact-inputs"}
                                    {...register("name")}
                                />



                                <input
                                    id="email_address"
                                    type="email"
                                    placeholder="Email Address"
                                    name="email"
                                    autoComplete="false"
                                    required
                                    className={light == "light" ? "contact-inputs contact-inputs-light" : "contact-inputs"}
                                    {...register("email")}
                                />



                                <textarea
                                    name="message"
                                    placeholder="Your Message"
                                    required

                                    className={light == "light" ? "contact-inputs contact-inputs-light" : "contact-inputs"}
                                    {...register("message")}
                                />


                                <button
                                    type="submit"

                                    className={light == "light" ? "submit-contact submit-contact-light" : "submit-contact"}
                                >
                                    Submit

                                </button>
                            </form>
                            {isSubmitSuccessful && isSuccess && (
                                <>
                                    <p className="sub-search" id="sent-message-one">
                                        Message sent successfully
                                    </p>
                                    {
                                        clearMsg()

                                    }

                                </>

                            )}
                            {isSubmitSuccessful && !isSuccess && (
                                <>
                                    <p className="sub-search" id="sent-message-two">
                                        Something went wrong. Please try again.
                                    </p>
                                    {
                                        clearMsgTwo()

                                    }
                                </>
                            )}
                            <br />
                            <p className={light == "light" ? "sub-search sub-search-light" : "sub-search"} >OR</p><br />
                            <div className="address" >
                                {/* <p className="add-det" >Contact mobile :- +91 9650566054</p> */}
                                <p className={light == "light" ? "email-at email-at-light" : "email-at"}>E-mail at <a href="mailto:sahilkalkal108@gmail.com" className={light == "light" ? "skill-click skill-click-light" : "skill-click"}>sahilkalkal108@gmail.com</a></p>
                                {/* <p className="add-det" >Operational Address :- G-79, Kunwar Singh Nagar, Nangloi, New Delhi - 110041</p> */}
                            </div><br />
                            <div className="social-icons">

                                <a href="https://github.com/sahilkalkal9" target="blank" ><img className="social-icon" src={github} /></a>
                                <a href="https://instagram.com/sahil.kalkal_" target="blank" ><img className="social-icon" src={instagram} /></a>
                                <a href="https://www.linkedin.com/in/sahil810/" target="blank" ><img className="social-icon" src={linkedin} /></a>
                                <a href="https://discordapp.com/users/991407816415907931" target="blank" ><img className="social-icon" src={discord} /></a>
                                <a href="https://twitter.com/sahilkalkal_" target="blank" ><img className="social-icon" src={twitter} /></a>




                            </div>




                        </div>
                    </Slide>
                </center>
            </div>
            <ScrollToTop smooth />
        </div>

    )
}

export default Contact;