import "./projects.scss"
import ct from "./chemictionary.png"
import strayfarm from "./strayfarm.png"
import redi from "../home/redirect.png"
import resume from "./resume.png"
import todo from "./todo.png"
import gsign from "./google.png"
import fauth from "./auth.png"
import socioease from "./socioease.png"
import iland from "./instagram.png"
import star from "./star.png"
import pt from "./pt.png"
import fi from "./folders.png"
import ScrollToTop from "react-scroll-to-top"
import nt from "./notes.png"
import axios from "axios"
import React, { useEffect, useState } from 'react';
import Bounce from 'react-reveal/Bounce';
import Flip from 'react-reveal/Flip';
import GoToTop from "../../top"

function Projects({ light }) {

    const [starsChem, setStarsChem] = useState(null);
    const [starsNotebook, setStarsNotebook] = useState(null);
    const [starsFile, setStarsFile] = useState(null);
    const [starsFolio, setStarsFolio] = useState(null);
    const [starsStray, setStarsStray] = useState(null);
    const [starsResume, setStarsResume] = useState(null);
    const [starsTodo, setStarsTodo] = useState(null);
    const [starsGoogle, setStarsGoogle] = useState(null);
    const [starsInsta, setStarsInsta] = useState(null);
    const [starsSocioEase, setStarsSocioEase] = useState(null);

    useEffect(() => {
        const fetchStars = async () => {
            try {
                const responseChem = await axios.get('https://api.github.com/repos/Chemictionary/chemictionary');
                const responseFolio = await axios.get('https://api.github.com/repos/sahilkalkal9/my-portfolio');
                const responseNote = await axios.get('https://api.github.com/repos/sahilkalkal9/notebook');
                const responseFile = await axios.get('https://api.github.com/repos/sahilkalkal9/file-space');
                const responseStray = await axios.get('https://api.github.com/repos/sahilkalkal9/strayfarm');
                const responseInsta = await axios.get('https://api.github.com/repos/sahilkalkal9/instaLandingPage');
                const responseGoogle = await axios.get('https://api.github.com/repos/sahilkalkal9/google-signin');
                const responseTodo = await axios.get('https://api.github.com/repos/sahilkalkal9/todo-app');
                const responseResume = await axios.get('https://api.github.com/repos/sahilkalkal9/resume-hub');
                const socioease = await axios.get('https://api.github.com/repos/socioease/socioease.github.io');

                setStarsChem(responseChem.data.stargazers_count);
                setStarsFile(responseFile.data.stargazers_count);
                setStarsFolio(responseFolio.data.stargazers_count);
                setStarsGoogle(responseGoogle.data.stargazers_count);
                setStarsInsta(responseInsta.data.stargazers_count);
                setStarsResume(responseResume.data.stargazers_count);
                setStarsNotebook(responseNote.data.stargazers_count);
                setStarsTodo(responseTodo.data.stargazers_count);
                setStarsStray(responseStray.data.stargazers_count);
                setStarsSocioEase(socioease.data.stargazers_count);

            } catch (error) {
                console.error('Error fetching stars:', error);
            }
        };

        fetchStars();
    }, []);

    // console.log(stars)




    const projects = [
        {
            "name": "SocioEase",
            "src": socioease,
            "stars": starsSocioEase,
            "year": "2023",
            "tagline": "A social media website",
            "desc": "SocioEase is a simple social media website which is strictly concerned about your privacy and it is distraction free also. (Under Development)",
            "code": "https://github.com/sahilkalkal9/socioease",
            "star": "https://github.com/sahilkalkal9/socioease",
            "demo": "https://socioease.vercel.app",
            "br": "100px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "Chemictionary",
            "src": ct,
            "stars": starsChem,
            "year": "2023",
            "tagline": "Dictionary of Chemistry",
            "desc": "Chemictionary is a simple, fast and easy platform to build the concepts of chemistry.",
            "code": "https://github.com/Chemictionary/chemictionary",
            "star": "https://github.com/Chemictionary/chemictionary",
            "demo": "https://chemictionary.com",
            "br": "0px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "Notebook",
            "src": nt,
            "stars": starsNotebook,
            "year": "2023",
            "tagline": "Your online notebook",
            "desc": "Notebook is your personal space to save your keypoints, todos and targets online.",
            "code": "https://github.com/sahilkalkal9/notebook",
            "star": "https://github.com/Chemictionary/chemictionary",
            "demo": "https://sahilkalkal9.github.io/notebook",
            "br": "0px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "File Space",
            "src": fi,
            "stars": starsFile,
            "year": "2023",
            "tagline": "Save files online",
            "desc": "File Space is your personal space to save any file online, file of any format can be saved.",
            "code": "https://github.com/sahilkalkal9/file-space",
            "star": "https://github.com/Chemictionary/chemictionary",
            "demo": "https://sahilkalkal9.github.io/file-space",
            "br": "0px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "Portfolio",
            "src": pt,
            "stars": starsFolio,
            "year": "2023",
            "tagline": "My portfolio Website",
            "desc": "It is basically about me, I tried to keep it simple and clean, hope you will like it.",
            "code": "https://github.com/sahilkalkal9/my-portfolio",
            "star": "https://github.com/Chemictionary/chemictionary",
            "demo": "https://sahilkalkal.com",
            "br": "0px",
            "tech": "React | CSS"
        },
        {
            "name": "Stray Farm",
            "src": strayfarm,
            "stars": starsStray,
            "year": "2023",
            "tagline": "Animal Rescue & Shelter",
            "desc": "Stray Farm is a non profit organisation, helping animals to live happily.",
            "code": "https://github.com/sahilkalkal9/strayfarm",
            "star": "https://github.com/sahilkalkal9/strayfarm",
            "demo": "https://strayfarm.me",
            "br": "50px",
            "tech": "React | CSS"
        },
        {
            "name": "Resume Hub",
            "src": resume,
            "stars": starsResume,
            "year": "2023",
            "tagline": "A Free Resume Builder",
            "desc": "Resume Hub is a free professional resume builder, have a try, hope you will like it.",
            "code": "https://github.com/sahilkalkal9/resume-hub",
            "star": "https://github.com/sahilkalkal9/resume-hub",
            "demo": "https://sahilkalkal9.github.io/resume-hub/",
            "br": "0px",
            "tech": "React | CSS"
        },
        {
            "name": "Todo App",
            "src": todo,
            "stars": starsTodo,
            "year": "2023",
            "tagline": "A Simple Todo App",
            "desc": "It is a simple to-do saving website, here you can save your todos, no limits are there.",
            "code": "https://github.com/sahilkalkal9/todo-app",
            "star": "https://github.com/sahilkalkal9/todo-app",
            "demo": "https://sahilkalkal9.github.io/todo-app/",
            "br": "0px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "Google SignIn UI",
            "src": gsign,
            "stars": starsGoogle,
            "year": "2023",
            "tagline": "Google Sign In UI",
            "desc": "It is a clone of the Google's official Sign In UI,tried to build same, hope you will like it.",
            "code": "https://github.com/sahilkalkal9/google-signin/",
            "star": "https://github.com/sahilkalkal9/google-signin/",
            "demo": "https://sahilkalkal9.github.io/google-signin/",
            "br": "0px",
            "tech": "React | CSS"
        },

        {
            "name": "Insta login page",
            "src": iland,
            "stars": starsInsta,
            "year": "2023",
            "tagline": "Instagram login UI",
            "desc": "It is a clone of the Instagram's official Sign In page, tried to build same, hope you will like it.",
            "code": "https://github.com/sahilkalkal9/instaLandingPage/",
            "star": "https://github.com/sahilkalkal9/instaLandingPage/",
            "demo": "https://sahilkalkal9.github.io/instaLandingPage/",
            "br": "0px",
            "tech": "React | CSS"
        },

        


    ]

    return (
        <div className="Projects">
            <Bounce left><p className={light == "light" ? "headingg headingg-light" : "headingg"} >Projects</p></Bounce><br />
            <Flip top cascade>
                <div className="projects-container">

                    {
                        projects.map((pt) => (

                            <div className={light == "light" ? "project project-light" : "project"} >
                                <div className="project-head">
                                    <p className={light == "light" ? "project-year project-year-light" : "project-year"} >{pt.year}</p>
                                    <a target="blank" href={pt.code}>
                                        <div className="header-project">
                                            <img className="star" src={star} />
                                            <p className="project-year starss" >{pt.stars}</p>
                                        </div>
                                    </a>

                                </div>
                                <div className="project-logo-container">
                                    <img src={pt.src} className="project-logo" alt="project logo" style={{ borderRadius: pt.br }} />
                                    <div className="project-name-container">
                                        <a target="blank" href={pt.code} ><p className={light == "light" ? "project-name project-name-light" : "project-name"}>{pt.name}</p></a>
                                        <p className={light == "light" ? "project-tagline project-tagline-light" : "project-tagline"}>{pt.tagline}</p>
                                    </div>
                                </div>
                                <p className={light == "light" ? "project-desc project-desc-light" : "project-desc"} >{pt.desc}</p>
                                <p className={light == "light" ? "tech tech-light" : "tech"}>{pt.tech}</p>

                                <br />

                                <div className="project-buttons">
                                    {/* <a href={pt.code} target="blank">
                                    <div className={light ? "code code-light" : "code"}>
                                        <p className={light ? "code-link code-link-light" : "code-link"}>Code</p>
                                        <img className="redirect predp" alt="redirect icon" src={redi} />
                                    </div>
                                </a> */}



                                    <a href={pt.demo} target="blank"><div className={light == "light" ? "code code-light" : "code"}>
                                        <p className={light == "light" ? "code-link code-link-light" : "code-link"}>Visit</p>
                                        <img className="redirect predp" alt="redirect icon" src={redi} />
                                    </div></a>
                                </div>
                            </div>

                        ))
                    }

                </div>
            </Flip>
            <ScrollToTop smooth />
            <GoToTop />
        </div>
    )
}
export default Projects;